.guilds-page {
    display: flex;
    flex-direction: column;
    height: 100%;
    flex: 1;

    .guilds-menu {
        clear: both;
        float: none;
        margin-left: auto;
    }

    .content-view {
        .sub-header {
            font-size: 24pt;
            display: flex;
        }

        flex: 1;
        height: 100%;
        clear: both;
        width: 60%;
        margin-left: auto;
        margin-right: auto;
        background-color: #181a1a;
        .inner-content-view {
            padding: 20px;
        }
    }

    .guild-details {
        .details-header {
            display: flex;

            .make-default-button {
                margin-left: auto;
                background-color: #332d5a;
                padding: 3px;
                cursor: pointer;
            }
        }

        .members-heading {
            margin-top: 25px;
            font-size: 14pt;
            display: flex;
        }

        .guild-members {
            display: flex;

            .guild-member {
                display: flex;
                padding: 3px;
                background-color: #024639;
                font-size: 8pt;
                margin-right: 15px;

                .user-address {
                    font-size: 4pt;
                }
            }
        }
        .guild-name {
            font-size: 20pt;
            background-color: #212121;
            padding: 3px;
            cursor: pointer;
        }
    }

    .guilds-list {
        .guild-item:hover {
            box-shadow: 0px 0 1px #1b2b75;

            .guild-name {
                box-shadow: 0px 0 1px #110505;
            }
        }

        .guild-item {
            cursor: pointer;
            display: flex;
            background-color: #0a0a35;
            padding-bottom: 5px;

            .guild-name {
                background-color: black;
            }

            .members-count {
                margin-left: auto;
                background-color: black;
                font-size: 10pt;
                padding-left: 5px;
                padding-right: 5px;
            }
        }
    }


    .add-guilds-button:hover {
        background-color: #055949;
    }

    .add-guilds-button {
        display: flex;
        flex-direction: row;
        padding: 10px;
        background-color: #014639;
        cursor: pointer;
        font-size: 20pt;

        .big-plus {
            font-size: 40pt;
            margin-right: 10px;
            line-height: 15px;
        }

        .button-text {
            line-height: 30px;
        }
    }

    .passphrase-label {
        font-size: 20pt;
    }

    .creating-new-guild {
        font-size: 18pt;
    }

    .potential-member {
        background-color: #04011a;
        padding: 3px;
        cursor: pointer;
        display: flex;
        height: 40px;

        .user-name {
            background-color: #002748;
            font-size: 14pt;
            line-height: 30px;
        }

        .user-id {
            font-size: 8pt;
            margin-left: auto;
        }
    }

    .potential-member.selected {
        background-color: red;
    }

    .add-members-header {
        font-size: 14pt;
        margin-bottom: 10px;
        margin-top: 10px;
    }

    .add-member-button {
        background-color: #45edcd;
        padding: 3px;
        cursor: pointer;
        font-size: 20pt;
        color: black;
        margin-left: 10px;
        height: 30px;
        line-height: 20px;
    }
}

#passphrase-input, #guild-name-input {
    background-color: black !important;
    color: white;
    font-size: 18pt;
    width: 400px !important;
    margin-bottom: 10px;
}


.guilds-menu {
    float: left;
    margin-right: 5px;
}

.link-requests {
    margin-left: auto;
    padding: 10px;
    background-color: #2b2a2a;
    border-radius: 3px;
}

.requests-header {
    font-size: 16pt;
    margin-bottom: 5px;
    
}

.no-requests {
    font-style: italic;
    color: #d4d4d4;
}

.new-member-request {
    display: flex;
    flex-direction: row;
    
    .action-description {
        padding: 5px;
    }
    
    .action-btns {
        margin-left: auto;
    }
    
    .guild-field {
        padding: 5px;
        background-color: #303955;
    }
    
    .user-field {
        padding: 5px;
        
        .user-hyperlink {
            padding: 5px;
            margin-left: 5px;
            background-color: #202023;
        }
    }
}

.guild-dropdown-option {
    display: flex;
}

.guilds-checkmark {
    margin-left: auto;
    line-height: 10px;
}

.small_checkmark {
    border-width: 1px;
    display: inline-block;
    width: 10px;
    height: 10px;
    transform: rotate(38deg);
}

.small_checkmark_stem {
    position: absolute;
    width: 3px;
    height: 14px;
    background-color: #5ec3ac;
    left: 10px;
    top: 2px;
}

.small_checkmark_kick {
    position: absolute;
    width: 6px;
    height: 3px;
    background-color: #5ec3ac;
    left: 6px;
    top: 13px;
}
