.flashit{
  color:#f2f !important;
	-webkit-animation: flash linear 1s infinite;
	animation: flash linear 0.5s infinite;
}
@-webkit-keyframes flash {
	0% { opacity: 1; } 
	50% { background-color: red; } 
	100% { opacity: 1; }
}
@keyframes flash {
	0% { opacity: 1; } 
	50% { opacity: .1; } 
	100% { opacity: 1; }
}
