.step.off.selection {
    background-color: red !important;
    opacity: 0.3 !important;
}

.track-name-view {
    font-size: 10pt;
}

.step.on.selection {
    opacity: 0.3 !important;
}

.step.selected {
    border-width: 2px;
}

.step.off {
    background-color: rgba(0,0,0,0.4);
    step-parameter {
        color: white;
    }
}

.step.on {
    .step-parameter {
        color: black !important;
        padding-left: 6px;
        padding-right: 6px;
    }
}

.effects-parameters-button-container {
    .small-button {
        margin-left: 5px;
        background-color: black !important;
    }
}

.step-container {
    .fill {
        height: 23px;
    }
}

.fill.current-step {
    border-bottom: 2px solid red;
}

.fill.cursor {
}

.fill.cursor.on {
}

.step.current-step {
}

@keyframes blink { 50% { border-color: #c4ffec; }  }
@keyframes blink2 { 50% { border-color: #f2ffc9; }  }

.step.cursor, .step.alternate.cursor {
    border-width: 2px;
    border-color: #ff549e;
}

.step.vi-mode.cursor {
    animation: blink .3s step-end infinite alternate; 
}

.step.on.cursor {
    animation: blink2 .3s step-end infinite alternate; 
}

.step.cursor {
    animation: blink2 .3s step-end infinite alternate; 
}

.step.on.vi-mode.cursor {
    border-top-color: #ff549e !important;
    border-right-color: #ff549e !important;
    border-left-color: #ff549e !important;
}

.step.on.cursor {
}

.step.off.cursor {
}

.step.off.vi-mode.cursor {
    border-color: #ff549e !important;
}

.steps-row.odd {
    .step {
        border-color: #1f3571
    }

    .step.alternate {
        border-color: #4b83ff;
    }
}

.step {
    border-color: #4b83ff;
}

.step.alternate {
    border-color: #1f3571;
}

.step-parameter {
    margin-bottom: 5px;
    color: white;
    display: block;
}

.step-position {
    font-size: 4pt;
    color: black;
    text-align: center;
}

.steps-row-bottom {
    width: 100%;
    height: 2px;

    .current-step-box {
        background-color: red;
        width: 35px;
        height: 2px;
    }
}

.step.not-expanded {
    width: 29px;
    height: 18px;
}

.step {

    .right-edge {
        margin-left: auto;
        width: 5px;
    }

    border-width: 1px;
    float: left;
    Cursor: pointer;
    border-style: solid;
    margin-left: 4px;
    margin-right: 4px;
    margin-bottom: 4px;
    margin-top: 2px;
    font-size: 14px;
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */

}

