$textColor: #c9c6c6;
$toastFlashColor: #ffffff;

.dashboard-module {
    position: relative;
    
    .rules-module {
        margin-top: 10px;

        .rule {
            margin-top: 5px;
        }
    }

    .room-module {
        margin-top: 15px;

        .people-online {
            font-size: 24pt;
            text-align: center;
            line-height: 30px;
        }

        .people-text {
            font-size: 12pt;
            text-align: center;
        }

        .leave-room {
            cursor: pointer;
            margin-top: 8px;
            background-color: #f3235e;
            color: black;
            text-align: center;
        }
    }

    .sequencer-module {
        .mute-button {
            margin-top: 15px;
            width: 100%;
            height: unset;
            padding: 0px;
        }
    }

    .actions-browsing-history-text {
        margin-top: 8px;
        font-size: 10pt;
        line-height: 16px;
        text-align: center;
    }

    .actions {
        .module-name {
            margin-top: 12px;
        }

        .undo-redo-buttons {
            display: flex;
            text-align: center;
            font-size: 8pt;
            position: absolute;
            left: 0px;
            top: 0px;

            .undo-button, .redo-button {
                margin-left: 0px;
                color: black;
                cursor: pointer;
            }

            .undo-button.disabled, .redo-button.disabled {
                opacity: 0.1;
                color: none;
                pointer-events: none;
            }

            .undo-button {
                background-color: #6cf89c;
                width: 63px;
            }

            .redo-button {
                background-color: #74eef4;
                width: 62px;
            }
        }

        .time-remaining {
            margin-top: 30px;
        }

        .actions-remaining {
            margin-top: 3px;
        }

        .actions-remaining, .time-remaining {
            font-size: 24pt;
            text-align: center;
            line-height: 30px;
        }

        .actions-text {
            font-size: 12pt;
            text-align: center;
            line-height: 15px;
        }

        .submit-button {
            margin-top: 17px;
            cursor: pointer;
            background-color: #6cffa7;
            color: black;
            text-align: center;
        }

        .submit-button.disabled {
            opacity: 0.1;
            pointer-events: none;
        }
    }

    .history {
        .history-slider {
            margin-top: 10px;
            text-align: center;
        }

        .history-timestamp {
            margin-top: 10px;
            font-size: 12pt;
            text-align: center;
        }

        .history-back-to-live {
            cursor: pointer;
            margin-top: 12px;
            background-color: #4ddafa;
            color: black;
            text-align: center;
        }

        .history-browse-history-text {
            margin-top: 12px;
            font-size: 22pt;
            text-align: center;
        }

        .history-browse-history {
            cursor: pointer;
            margin-top: 22px;
            background-color: #eca1f7;
            color: black;
            text-align: center;
        }
    }

    .toast-text {
        color: $textColor;
        margin-top: 18px;
        font-size: 12pt;
        text-align: center;
    }

    .toast-close {
        position: relative;
        
        .close {
            cursor: pointer;
            position: absolute;
            font-size: 12pt;
            color: $textColor;
            right: 4px;
            top: -1px;
        }
    }

    .mixer-module {
        display: flex;
        flex-wrap: wrap;
        overflow: hidden;

        .track-mute.mute {
            opacity: 0.2;
        }

        .track-mute.selected, .track-mute.triggering {
            box-shadow: 0px 0px 5px white;
        }

        .track-mute { 
            margin: 5px;
            width: 20px;
            padding: 3px;
            height: 20px;
            text-align: center;
            background-color: #41f4ae;
            color: black;
            cursor: pointer;
        }
    }
}

.hide-module {
    position: relative;
    order: 2;
    margin-left: auto;
    margin-right: 0px !important;
    text-align: center;

    .hide-eye {
        cursor: pointer;
        filter: invert(1);
        margin-top: 12px;
        opacity: 65%;
        font-size: 24pt;
        width: 52px;
    }

    .hide-eye-slash {
        cursor: pointer;
        filter: invert(1);
        margin-top: 8px;
        opacity: 65%;
        width: 60px;
    }

    .hide-text {
        position: absolute;
        font-size: 10pt;
        bottom: 12px;
        padding: 2px;
    }
}

.toast-border {
    box-shadow: 0 0 8px $toastFlashColor;
}

.toast-flash {
    animation: toast-anim 5s;
    -mox-animation: toast-anim 5s;
    -webkit-animation: toast-anim 5s;
    -o-animation: toast-anim 5s;
}

@keyframes toast-anim {
    from { box-shadow: 0 0 35px $toastFlashColor; }
    to   { box-shadow: default; }
}
@-moz-keyframes toast-anim { /* Firefox */
    from { box-shadow: 0 0 35px $toastFlashColor; }
    to   { box-shadow: default; }
}
@-webkit-keyframes toast-anim { /* Safari and Chrome */
    from { box-shadow: 0 0 35px $toastFlashColor; }
    to   { box-shadow: default; }
}
@-o-keyframes toast-anim { /* Opera */
    from { box-shadow: 0 0 35px $toastFlashColor; }
    to   { box-shadow: default; }
}

.roll-buttons {
    display: flex;
    flex-wrap: wrap;
    margin-left: 5px;

    .explore-roll-button {
        cursor: pointer;
        width: 50px;
        text-align: center;
        padding: 10px;
        background-color: #42404f;
        margin: 2px;
    }

    .explore-roll-button.rolling {
        background-color: red;
    }
}


