.instrument.instrument-rack {
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: #0e0e0e;
    display: flex;
    min-width: auto;
    margin-right: 20px;
    border-right-color: #6d6d6d;
    border-right-style: dashed;

    .effects-view {
        width: 100%;
        overflow: hidden;
        flex: 1;
    }

    .toolbar {
        padding-top: 0px;
        display: flex;

        .toolbar-button.selected {
            background-color: #3a54e6;
        }

        .toolbar-button {
            padding: 3px;
            background-color: #0a1759;
            margin-left: 0px;
            cursor: pointer;
            margin-right: 5px;
        }
    }

    .instrument {
        background-color: auto;
    }

    .instrument-rack-controller {
        display: flex;
        flex-direction: column;

        background-color: #2d2d2f;
        min-width: 100px;

        .multiple-parameter-slider {
            margin-top: 5px;
            border-top: 1px solid #61686f;;

            .slider-horizontal {
                width: 110px;
            }
        }

        .drop-here {
            min-height: 100px;
            background-color: #1b1c23;
            display:table;
            text-align: center; 
            padding: 5px;

            span {
                display:table-cell;
                vertical-align: middle;
            }
        }

        .track-slot {
            cursor: pointer;
            display: flex;

            .sub-instrument-name {
                padding-left: 3px;
                margin-right: 5px;
                width: 100px;
                overflow: hidden;
                white-space: nowrap;
            }

            .delete-button {
                margin-top: 3px;
                margin-left: auto;
                border: none;
                background: none;
            }
        }

        .track-slot.selected {
            color: black;
        }
    }
}

.volume-box {
    position: relative;
    border: 1px solid black;
    background-color: #2d2c2f; 
    color: white;
    text-align: center;
    height: 26px;
    font-size: 8pt;
    
    .volume-indicator {
        position: absolute;
        background-color: red;
        opacity: 0.5;
        height: 24px;
    }
}

