.effect-header {
    .macro-map-button {
        margin-left: auto;
        border: 1px solid #5c5b5b;
        background-color: #29293f;
        cursor: pointer;
        padding-left: 3px;
        padding-right: 3px;
        margin-right: 5px;
        font-size: 8pt;
    }

    .macro-map-button.mapping {
        background-color: #31d76b;
        color: black;
        border-color: #31d76b;
    }
}

.macros-container {
    display: flex;
    flex-wrap: wrap;
    width: 160px;

    .macro-knob {
        font-size: 8pt;
        margin-right: 15px;
        margin-bottom: 10px;
        height: 60px;

        .map-button {
            cursor: pointer;
            margin-top: 2px;
            background-color: #332f2f;
            color: #b0bcc3;
            border: 1px solid #6c757a;
            border-radius: 6px;
            padding-left: 3px;
            padding-right: 3px;
            width: 34px;
            text-align: center;
            font-size: 7pt;
        }
    }
}

.macro-details {
    padding: 5px;

    .name-editor {
        width: 100%;
        background-color: black;
        color: white;
    }
    .sub-macro {
        display: flex;

        .sub-field.effect, .sub-field.parameter {
            max-width: 60px;
            margin-left: 0px;
        }

        .sub-field {
            margin-right: 5px;
        }

        .min-field, .max-field {
            width: 35px;
            background-color: black;
            color: white;
        }
    }
}


.parameter-button {
    position: relative;
    
    .macro-overlay {
        left: 0px;
        top: 0px;
        width: 40px;
        height: 40px;

        flex-direction: column;

        .overlay-detail {
            display: none;
            height: 20px;
            opacity: 0.3;
        }
    }

    .macro-overlay.mapped {
        .overlay-detail {
            display: block;
        }
    }

    .macro-overlay.parameter-to-map {
        opacity: 1;

        .overlay-main {
            opacity: 0.7;
        }

        .overlay-detail {
            opacity: 1;
        }
    }

}

.macro-overlay.parameter-to-map {
    border: 1px dashed white;
    opacity: 0.6;
}

.macro-overlay {
    position: absolute;
    display: flex;
    z-index: 300;
    width: 180px;
    height: 40px;;
    cursor: pointer;
    .overlay-main {
        opacity: 0.4;
        background-color: #31d76b;
        flex: 1;
    }

    .overlay-detail {
        opacity: 0.4;
        width: 40px;
        height: 40px;
        background-color: #31d76b;
        font-size: 8pt;
        color: black;
        text-align: center;
    }
}

.macro-overlay.mapped {
    .overlay-detail {
        opacity: 1;
    }
}


.effect-details {
    .macro-details {
        padding: 0px;
        border: 1px solid #232326;
        margin: 5px;


        .min-field, .max-field {
            width: 50px;
        }
        .macro-name {
            background-color: #1e1e21;
            opacity: 0.5;
            color: #a9ccff;
        }
    
        .sub-field.parameter, .sub-field.effect {
            border: none;
            width: 100px;
            max-width: 100px;
        }

        .sub-macro {
            display: flex;
            border-bottom: 1px solid #1d1d21;
            padding: 3px;
        }
    }
}
