.effect-rack {
    .horizontal {
        display: flex;
    }

    .rack-toolbar {
        display: flex;
        width: 500px;

        .add-macro-button {
            margin-left: 5px;
            border-radius: 2px;
            background-color: #252525;
            padding-left: 5px;
            padding-right: 5px;
            line-height: 25px;
            cursor: pointer;
        }

        .show-macros-button {
            margin-left: auto;
            border-radius: 2px;
            background-color: #252525;
            padding-left: 5px;
            padding-right: 5px;
            line-height: 25px;
            cursor: pointer;
        }
    }

    .draw-component {
        background-color: #2b2d3c;
    }

    .effect-dropdown {
        .Dropdown-control {
            width: 70px;
            height: 25px;
            padding: 4px;
            border: none;

        }
        .Dropdown-menu {
            width: 100px;
            max-height: 300px;

            .Dropdown-option {
                padding: 3px;
            }
        }
    }

    .expand-macros-button {
        padding: 3px;
        background-color: #272727;
        color: white;
        width: 100%;
        text-align: center;
        cursor: pointer;
    }
}
