
.rack-modal {
    max-width: 400px !important;
}

.sequence-action {
    display: flex;
    flex-direction: row;
}

.sequence-action-name {
    margin-right: 5px;
}

.clear-pattern-button {
    margin-left: 0px !important;
}

.resample-action {
    margin-top: 5px;
    display: flex;
    flex-direction: row;
}

.tag-input {
    background-color: black !important;
    color: white !important;
    border-style: solid;
    border-color: #383f46 !important;
}

.buy-action {
    cursor: pointer;
    color: black;
    font-size: 8pt;
    padding: 5px;
    background-color: #62ffa1;;
}
.balance {
    margin-right: 20px;
}

.price {
    margin-top: 5px;
}

.price-eth, .price-dollars {
    color: white;
    margin-right: 5px;
}

.price-eth-wrapper, .price-dollars-wrapper {
    padding: 5px;
    background-color: #127543;
    color: white;
    font-size: 8pt;
    margin-left: 5px;
}

.waiting-for-tokens {
    font-size: 10pt;
    color: #a5b3d3;
}

.go-to-buy {
    margin-left: 20px;
}

.balance-info {
    font-size: 10pt;
}

.ethereum-explanation {
    margin-top: 15px;
    font-size: 10pt;

    .basic-explanation {
        margin-bottom: 10px;
    }
}

.input-wrapper {
    display: flex;
}

.metamask-instructions {
    font-size: 10pt;
}

#linkInput {
    width: 400px;
    background-color: #232b4b;
    color: white;
}

.copied-info {
    clear: both;
    font-size: 10pt;
    color: #75fe75;
    background-color: black;
    padding: 3px;
    text-align: center;
}

.link-button {
    padding: 3px;
    color: white;
    background-color: #212d68;
    text-align: center;
    cursor: pointer;
}

.no-metamask {
    font-size: 10pt;
    margin-bottom: 10px;
}

.password-user-name {
    padding: 3px;
    background-color: #111523;
    color: #f4215e;
}

.guild-options-container {
    padding: 5px;
    background-color: #121222;
    border: 1px solid #191920;
    font-size: 11pt;
    
    .guild-options {
        display: flex;
        
        .guild-option {
            padding: 3px;
            background-color: gray;
            cursor: pointer;
            margin-right: 10px;
        }
        
        .guild-option.selected {
            background-color: #004eff;
        }
    }
}


.save-recordings {
    font-size: 10pt;

    .save-recordings-button {
        margin-left: 3px;
        padding: 1px;
        background-color: red;
        color: white;
        cursor: pointer;
    }
}
