$trackColor: #171718;
$altTrackColor: #0f0f10;

.track.selected {
    background-image: linear-gradient(#000000,#270202);
    border-radius: 8px;
}

.annotation {
    position: absolute;
    opacity: 0.95;

    .draggable-header {
        cursor: pointer;
        height: 10px;
    }

    .delete-button {
        color: white !important;
        background: none;
        border: none;
        height: 10px;
    }

    .annotation-text {
        color: black !important;
        font-size: 10pt;
        min-width: 200px;
        padding: 0px;
        border: none;
        padding-left: 3px;
        padding-right: 3px;
    }
}

.track.odd {
    border-color: $altTrackColor;
}

.track {
    border-radius: 0px;
    clear: both;
    border-color: $trackColor;
    margin-bottom: 2px;
    border-style: solid;
    border-width: 5px;
    padding-bottom: 2px;
    margin-right: 10px;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; 

    .volume-box {
        .volume-indicator {
            background-color: blue;
            cursor: col-resize;
        }
    }

    .youtube-container {
        padding-right: 5px;
        .youtube-thumbnail {
            margin-left: 0px;
        }
    }

    .track-right-buttons {
        float: none;
        flex: 1;

        .effects-section {
            width: 25px;
            border-radius: 0px;
            padding-left: 5px;
        }
    }
    
    .track-inner {
        display: flex;
        margin-top: 3px;
        padding-top: 3px;
   }

    .track-header.odd {
        background-color: $altTrackColor;
    }

    .track-header {
        display: flex;
        background-color: $trackColor;

        .sample-tag-input {
            font-size: 8pt;
            margin-right: 5px;
        }

        .add-sample-tag-button {
            margin-right: 5px;
            background-color: black;
            line-height: 14px;
            color: red;
            padding-left: 3px;
            padding-right: 3px;
            cursor: pointer;
        }

        .track-name-view {
            border-top-right-radius: 3px;
            padding-left: 4px;
            padding-right: 4px;
            font-size: 10pt;
            background-color: #18181a;
        }

        .delete-button {
            margin-right: 5px;
            margin-left: 5px;
        }

        .track-tags {
            margin-left: auto;
            display: flex;

            .track-tag.highlighted {
                background-color: red;
            }

            .track-tag {
                font-size: 8pt;
                padding-left: 3px;
                padding-right: 3px;
                background-color: #041219;
                border-radius: 2px;
                margin-right: 2px;
            }
        }
    }
}

.track.flex.compact {
    border-width: 6px;
}

.midi-channel-container, .midi-input-dropdown-container {
    display: flex;
    .midi-input-dropdown {
        .midi-input-control-dropdown {
            padding-left: 5px;
            display: flex;
            flex: 1;
            height: 12px;
            .Dropdown-placeholder {
                line-height: 4px;
            }

            .Dropdown-arrow-wrapper {
                .Dropdown-arrow {
                    top: 8px !important;
                }
            }
            
        
        }
    }
    margin-top: 5px;
}

.midi-input-dropdown {
    width: 60px;
}

.audio-input-dropdown-container {
    height: 20px;
}

.midi-option {
    font-size: 6pt;
    cursor: pointer;
    border-style: solid;
    border-radius: 2px;
    border-width: 1px;
    margin: 5px;
    padding: 3px;
}

.midi-input {
    clear: both;
    margin-bottom: 10px;
}

.midi-output {
    clear: both;
    margin-bottom: 10px;
}

.inputs-heading, .outputs-heading {
    clear: both;
    font-size: 20px;
    color: #f4f6ff;
}

.midi-option.enabled {
    background-color: red;
    color: white;
}

.midi-channel-container {
    display: flex;

    .midi-channel {
        margin-right: 5px;
    }
}

.midi-outputs-container {
    margin-top: 5px;
    width: 400px;
    display: flex;
    flex-wrap: wrap;
}

.react-contextmenu {
    background-color: #1d2122;
    border-style: solid;
    border-width: 1px;
    border-color: #3e3232;

    .react-contextmenu-item:hover {
        background-color: black;
    }

    .react-contextmenu-item {
        cursor: pointer;
        padding: 5px;
        
    }

}

.frozen-container {
    display: flex;
    margin-top: 10px;
    margin-left: 10px;
    background-color: #303030;
    height: 30px;
    width: 300px;
    border: solid 2px #353946;

    .freezing-label {
        font-size: 10pt;
        color: #353946;
        padding: 5px;
        color: #a0add8;
    }

    .playpause-user {
        float: none;
        margin-top: 0px;
        margin-right: 10px;
    }

    .frozen-progress {
        background-color: black;
        display: flex;
        flex: 1;

        .progress-part {
            border-right: solid 1px #303030;
            width: 6.25%;
            background-color: #505d69;
        }
    }
}

.waveform-container {
    .warp-button {
        float: left;
        padding: 5px;
        margin-left: 5px;
        font-size: 10pt;
        margin-top: 0px;
        margin-right: 10px;
    }
}
.warp-button {
    background-color: gray;
    padding-left: 5px;
    margin-top: 5px;
    border-radius: 5px;
    cursor: pointer;
}

.warp-button.selected {
    background-color: #5802d9;
}


.track-controls {
    display: flex;
    margin-left: 10px;

    .volume-container {
        max-width:150px;
    }
    
    .volume-container > .slider {
        height: 60px;
        margin-bottom: 5px;
    }
    
    .ghost-meter {
        height: 60px;
        width: 15px;
        border: solid 1px black;
        margin-left: 5px;
    }

    .track-meter {
        border: solid 1px #7000ff;
        width: 15px;
        height: 60px;
        margin-left: 5px;
        position: relative;

        .track-meter-reference {
            position: absolute;
            bottom: 0px;
            border-top: solid 1px #7000ff82;
            width: 14px;
        }

        .track-meter-value.is-red {
            background-color: red;
        }

        .track-meter-value {
            position: absolute;
            bottom: 0px;
            background-color: #41f4ad;
            width: 13px;
        }
    }
}

.expand-sample {
    font-size: 8pt;
    padding: 3px;
    background-color: #012b4f;
    margin-left: auto;
    width: 40px;
    cursor: pointer;
}

.sequencer-container.focused {
    .tracks-container {
        max-width: none !important;
    }
}

.steps-container {
    max-width: 4000px;
}

.steps-container {
    overflow: hidden;
    float: left;
    padding-left: 5px;
}

.lfos-track.track {
    display: flex;

    .lock-button {
        opacity: 1;
    }
}


.steps-map {
    .play-tick {
        position: absolute;
        height: 40px;
        width: 1px;
        background-color: red;
    }

    border: 1px solid #537bd4;
    height: 40px;
    width: 100%;
    margin-bottom: 10px;

    display: flex;
    position: relative;

    .part {
       flex-grow: 1;
       flex-basis: 0; 
       height: 100%;
       border-right: solid 1px #2a3d65;
       cursor: pointer;
    }

    .part:hover {
        background-color: black;
    }

    .part.current-part {
        background-color: #28282c;
    }

    .on, .off {
        height: 2px;
        position: absolute;
    }

    .on {
    }
}

.resample-name-input {
    background-color: black;
    color: white;
}

.track-loading-spinner {
    padding-top: 20px;
    padding-left: 8px;
}


.sends-and-options-container {
    .sends-container {
        display: flex;
    }

    .track-output-type {
        border: solid 1px black;
        border-radius: 3px;
        font-size: 8pt;
        background-color: red;
        margin-left: 10px;
        padding: 3px;
        height: 45px;
        background-color: #081735;

        .Dropdown-root.is-open {
            margin-bottom: 50px;
        }

        .Dropdown-option {
            padding: 3px;
        }

        .Dropdown-control {
            border: 1px solid #4e5058;
            width: 80px;

        }
    }
}

.track {
    .add-sample-tag-button {
        display: none;
    }
    .add-steps-button, .remove-steps-button, .lock-button {
        opacity: 0.3;
    }
}

.track.selected {
    .track-name-view {
        background-color: #191407;
    }

    .add-steps-button, .remove-steps-button, .lock-button, .effects-section {
        opacity: 1;
    }
}
