.piano-roll-container {
    clear: both;
    padding-top: 5px;
}

.piano-roll-wrapper {
    display: inline-block;
    position: relative;
}

.piano-roll-toolbar {
    display: flex;
    margin-bottom: 10px;
}
.transcribe-button {
    font-size: 10px;
    padding: 5px;
    cursor: pointer;
    margin-right: 5px;
    background-color: #00c27a;
    color: #4a0303;
    border-color: #2e4032;
    border-style: solid;
    border-width: thin;
}

.pagination-buttons {
    display: flex;
    flex-direction: row;
    flex-shrink: 1;
    float: left;
}

.pagination-buttons > .page-info {
    padding: 5px;
    margin-right: 5px;
    color: #ffa1a1;
}

.pagination-buttons > .unlock-button {
    padding: 5px;
    cursor: pointer;
    margin-right: 5px;
    background-color: #f92360;
    color: white;
    border-color: #2e4032;
    border-style: solid;
    border-width: thin;
}

.pagination-buttons > .previous-page-button, .next-page-button {
    border-style: solid;
    border-width: thin;
    flex-grow: 2;
    padding: 5px;
    cursor: pointer;
    border-color: #2e4032;
    color: #ffa1a1;
}

.piano-roll-time-row {
    clear: both;
    height: 30px;
    display: flex;
    background-color: #16151e;
    color: black;
    z-index: 0;
}

.note-name {
    font-size: 8px;
    text-align: center;
    cursor: pointer;
    width: 20px;
}

.grid-step {
    width: 36px;
}

.grid-step.selection {
    background-color: red !important;
}

.note-name {
    border-style: solid;
    border-width: thin;
    border-color: #633d5e;
}

.piano-roll-row.black {
    background-color: black;
    border: none !important;
}

.showing-automation {
    .piano-roll-row {
        background: none;
    }
}
.piano-roll-row {
    clear: both;
    display: flex;
    // border-top: 1px solid #141727;
    background-color: #0e101b;
    z-index: 2;
}


.note-name, .grid-step {
    float: left;
    border-left-style: solid;
    border-left-width: thin;
    border-left-color: #633d5e;
    color: #ffa1a1;
}

.time-grid-step.odd {
    background-color: #18182f;
    opacity: 0.2;
    border-color: #4a4569;
}

.time-grid-step {
    width: 36px;
    height: 450px;
    border-left-style: solid;
    border-width: thin;
    border-color: #24272f;
}

.time-grid-step.bar {
    border-left: 2px solid #261f4f;
    font-size: 8pt;
    padding-left: 2px;
}

.time-grid-step.current-step {
    border-left-color: red !important;
}

.time-grid-step.odd.current-step {
    background-color: none;
}

.grid-step {
    cursor: pointer;
    opacity: 0.5;
}

.note-name.white-note {
    background-color: white;
    color: black;
}

.grid-step.current-step {
    border-color: red !important;
}

.grid-step.alternate {
    border-color: #196143;
}

.grid-step.on {
    opacity: 1 !important;
}

.grid-step.on.alternate {
    opacity: 1 !important;
}

.grid-step.fill {
    background-color: #384e45;
    opacity: 0.8;
}

.grid-step.fill.alternate {
    background-color: #384e45;
    opacity: 0.8;
}

.left-edge {
    cursor: col-resize;
    width: 10px;
    float: left;
    height: 100%;
}

.right-edge {
    cursor: col-resize;
    width: 8px;
    height: 100%;
    margin-left: 12px;
}

.resample-duration-button {
    font-size: 10px;
    padding: 5px;
    cursor: pointer;
    margin-right: 5px;
    background-color: #f30054;
    color: white;
    border-color: #691029;
    border-style: solid;
    border-width: thin;
}

.piano-roll-wrapper {
    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.note-side-info {
    display: flex;
    min-width: 56px;
}

.note-blank-side {
    flex: 1;
    background-color: #212221;
    color: white;
    font-size: 8pt;
    text-align: center;
}

.note-name.triggering {
    background-color: red;
}

.note-blank-side.note-c {
    border-bottom-style: solid;
    border-bottom-width: 1px;
    overflow: visible;
    line-height: 100%;
}

.grid-steps-container {
    display: flex;
    height: 100%;
    width: 100%;
    z-index: 3;
}

.toggled-piano-roll-steps-container {
    position: absolute;

    .right-edge {
        float: right;
    }
}

.grid-step.toggled-grid-step {
    position: absolute;
}

.grid-step.selected {
    border-style: solid;
    border-left-width: 2px;
}

.track-length-container {
    display: flex;
    height: 35px;
    background-color: #202220;
    padding: 5px;

    .length-label {
        margin-right: 10px;
    }

    .osc-type-control-dropdown {
        height: 30px !important;
    }
}

.piano-roll-selection {
    background-color: red;
    opacity: 0.5;
    position: absolute;
}

.track {
    .banner-back-button {
        font-size: 10pt;
    }

    .banner-heading {
        font-size: 14pt;
    }
}

.piano-roll-cursor {
    position: absolute;
    height: 450px;
    width: 1px;
    background-color: orange;
}

.whole-wrapper {
    display: flex;

    .notes-side-panel {
        margin-top: 30px;
    }

    .piano-roll-fade-out {
        background-color: black;
        opacity: 0.6;
        flex: 1 1;
        min-width: 400px;
        height: 450px;
        z-index: 10;
    }
}

.piano-roll-rows-container {
    z-index: 2;
}

.grid-step.guide-step {
    pointer-events: none;
    border: none;
    opacity: 0.2 !important;
    filter: grayscale(70%);
}

.grid-step {
    font-size: 8pt;
    overflow: hidden;
    color: black;
}

.note-side-info.disabled-by-constraint {
    pointer-events: none;
    background-color: black !important;
    opacity: 0.1;
}

.installation {
    .piano-roll-container {
        .time-grid-step.odd.disabled-by-constraint {
        }
        
        .time-grid-step.disabled-by-constraint {
            pointer-events: none;
            z-index: 4900;
            filter: grayscale(10%);
            opacity: 0.8;
        }

        .piano-roll-row.disabled-by-constraint {
            position: relative;
            opacity: 0.4;
            pointer-events: none;
            
            .constraint-overlay {
                position: absolute;
                left: 0px;
                top: 0px;
                filter: grayscale(10%);
                opacity: 0.6;
                z-index: 1000;
            }
        }
    }
}
