.magenta-button {
    background-color: #8206d0
}

.btn {
    border-radius: 0px;
}

.common-button {
    float: left;
    margin-left: 5px;
    cursor: pointer;
    padding-right: 5px;
    font-size: 10pt;
    padding-left: 6px;
    padding: 5px;
    border-radius: 3px;
    line-height: 20px;
}

.small-button {
    float: left;
    cursor: pointer;
    padding-right: 2px;
    font-size: 8pt;
    padding-left: 2px;
}

.pink-button {
    background-color: #f92360;
    border-style: solid;
    border-color: #211b1b;
    border-width: thin;
}

.light-blue-button  {
    background-color: #333333;
    color: white;
    border-color: #211b1b;
    border-width: thin;
    border-style: solid;
}

.gray-button {
    background-color: #333333;
    border-style: solid;
    border-width: thin;
    border-color: #75797b;
}

.blue-button {
    background-color: #082434;
    border-style: solid;
    border-width: thin;
    border-color: #082434;
}

.panel {
    border-color: #031520;
    background-color: #0c0c0c;
    border-radius: 5px;
    border-style: solid;
    border-width: 10px;
    padding: 10px;
}

.Dropdown-control {
    background-color: #252525 !important;
    color: white !important;
}

.Dropdown-menu {
    background-color: #242424 !important;
    color: white !important;
    opacity: 0.97 !important;
              
}

.Dropdown-option {
    color: white !important;
}

.Dropdown-option:hover {
    color: black !important;
}

.Dropdown-option.is-selected {
    color: black !important;
}

.red-button {
    background-color: red;
}
