.dot {
  height: 12px;
  width: 12px;
  cursor: pointer;
  background-color: #92f4f6;
  border-radius: 50%;
  display: inline-block;
}

.recording-dot {
    height: 15px;
    width: 15px;
    background-color: red;
    cursor: pointer;
    border-radius: 50%;
    display: inline-block;
}

.black {
    background-color: black;
}

.plain-dot {
    margin-left: 8px;
    height: 5px;
    width: 5px;
    margin-bottom: 11px;
    cursor: pointer;
    text-align: center;
    border-radius: 50%;
    display: inline-block;
}

.preparing-dot {
    height: 15px;
    width: 15px;
    background-color: #42f4ad;
    cursor: pointer;
    border-radius: 50%;
    display: inline-block;
}

.record-audio-button {
    margin-left: 7px;
    margin-top: 35px;
}

.playpause label.mix {
  border-color: transparent transparent transparent #41f4ad;
}

.playpause label {
  display: block;
  margin-top: 5px;
  float: left;
  box-sizing: border-box;
  height: 12px;
  border-color: transparent transparent transparent white;
  transition: 100ms all ease;
  cursor: pointer;
  border-style: double;
  border-width: 0px 0 0px 12px;
  margin-right: 5px;
}

.not-recording-dot {
    height: 13px;
    width: 13px;
    cursor: pointer;
    background-color: white;
    border-radius: 50%;
    display: inline-block;
}

.playpause input[type="checkbox"] {
  position: absolute;
  left: -9999px;
}
.playpause input[type="checkbox"]:checked + label {
  border-style: solid;
  height: 10px;
  border-width: 6px 0 6px 8px;
}
.playpause input[type="checkbox"]:focus + label {
  box-shadow: 0 0 6px lightblue;
}

.recording {
    clear: both;
    margin-top: 20px;
}

.recordings-header {
    float: left;
}

.expand-to-tracks-button {
    float: left;
    margin-left: 20px;
}

.track-solo-button {
    width: 20px;
    height: 8px;
    font-size: 5pt;
    text-align: center;
    color: black;
    cursor: pointer;
    margin-bottom: 3px;
}

.track-solo-button.off:hover {
    background-color: #444561 !important;
    color: white;
}

.track-solo-button.off {
    background-color: grey;
}

.track-solo-button.on {
    background-color: #090d9a;
    color: white;
}

.midi-recording-button {
    width: 20px;
    height: 8px;
    cursor: pointer;
}

.midi-recording-button.off {
    background-color: grey;
}

.midi-recording-button.off:hover {
    background-color: #b19393;
}

.midi-recording-button.on {
    background-color: #e02828;
}

.mute-button {
    width: 20px;
    height: 20px;
    font-size: 8pt;
    margin-bottom: 3px;
    text-align: center;
    padding-top: 3px;
    color: black;
    background-color: #42f4ad;
    cursor: pointer;
}

