.collapse-toggle {
    width: 14px;
    height: 14px;
    border-width: 2px;
    border-style: solid;
    border-radius: 100%;
    margin-left: 3px;
    margin-right: 3px;
    border-color: #a2a2a2;
    background-color: #252525;
    cursor: pointer;
}

.effect.selected {
    border: solid 1px #bce0ff;
}

.arrow-right {
  width: 0; 
  height: 0; 
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-left: 7px solid #a2a2a2;
  margin-top: 2px;
  margin-left: 2px;
}

.arrow-down {
  width: 0; 
  height: 0; 
  margin-top: 3px;
  margin-left: 2px;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 7px solid #a2a2a2;
}

.effects-view {
    float: left;
    display: flex;
    flex-wrap: nowrap;
    overflow: auto;
    align-items: flex-start;
}

.effect {
    height: auto;
    align-content: flex-start;
    border-radius: 5px;
    margin-bottom: 5px;
    border: solid 1px black;
    display: block;
    font-size: 10pt;
    margin-left: 10px;
    float: left;
}

.inner-effect {
    padding: 10px;
}

.effect.enabled {
    background-color: #081735;
    
}

.selected-parameter {
    background-color: black;
}

.effect.disabled {
    background-color: grey;
}

.effect.disabled, .effect.collapsed {

    .effect-header {
        min-width: 20px !important;
    }
    .effect-name {
        flex: 1;
        color: black;
        min-width: 1px !important;
        font-size: 12pt;
        font-weight: bold;
        width: 1px;
        text-align: center;
        text-orientation: sideways;
        writing-mode: vertical-rl;
        margin-left: 20px;
    }
}

.effect.collapsed {
    .effect-name {
        color: white !important;
    }
}

.effect.disabled {
    .effect-header {
        background-color: #d6d4d4 !important;

        .effect-toggle {
            border-color: gray !important;

            .toggle-icon {
                background-color: gray !important;
            }
        }
    }
}

.effect-header {
    display: flex;
    min-width: 200px;
    padding-left: 5px;
    border-radius: 10px;
    padding-right: 5px;
    padding-top: 3px;
    flex-direction: row;
    background-color: #04131f;
}

.main-area {
    .delete-button {
        background-color: #252525;
    }
}

.delete-button {
    background-color: #252525;
    border-style: solid;
    border-width: 2px;
    border-radius: 100%;
    border-color: #651b1b;
    width: 14px;
    height: 14px;
    font-size: 12pt;
    text-align: center;
    padding-top: 0px;
}

.effect-toggle {
    border-style: solid;
    border-radius: 100%;
    border-color: #ffd000;
    font-size: 6pt;
    color: #ffd000;
    text-align: center;
    cursor: pointer;
    width: 14px;
    height: 14px;
    border-width: 2px;
    background-color: #252525;

    .toggle-icon {
        height: 6px;
        width: 2px;
        background-color: #ffd000;
        margin-top: 2px;
        margin-left: 4px;
    }
}

.effect-name {
    font-size: 10pt;
    margin-left: 8px;
    margin-right: 5px;
}

.mods-button {
    background-color: #24159f;
    border-radius: 2px;
    margin-left: auto;
    margin-right: 10px;
}

.parameter-mod {
    display: flex;
    flex-direction: row;
    width: 350px;
}

.parameter-mod-parameter, .parameter-mod-modulator {
    color:#eef5f2;
    margin-right: 5px;
}

.parameter-mod-text {
    color: #ffdddd;
    margin-right: 5px;
}

.parameter-mod-min, .parameter-mod-max {
    color: #ffdddd;
    margin-right: 5px;
}

.number-input {
    width: 75px !important;
    height: 20px !important;
}

.number-input-container {
    margin-left: 5px;
    float: right;
}


.parameter-mod-pieces {
    display: flex;
    flex-direction: row;
}

.add-effect-type {
    margin-bottom: 15px;
}

.parameter-dropdown-container {
    display: flex;
}

.effect-lock {
    margin-top: auto;
}

.convolver-thumbnail {
    width: 180px;
    height: 30px;
    
}

.sidechain-container {
    display: flex;
    
    .parameter-label {
        margin-right: 10px;
    }

    .osc-type-dropdown {
        width: 100px;
    }
}

.spectrum-container {
    clear: both;
    border-style: solid;
    border-width: 2px;
    border-color: #651212;
}

.equalizer-points {
    display: flex;
    flex-direction: row;
}

.equalizer-point-info.disabled {
    opacity: 0.1;
}

.equalizer-point-info.selected {
    color: black;
    background-color: white;
}

.equalizer-point-info {
    background-color: black;
    cursor: pointer;
    color: white;
    margin-left: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: black;
    padding-left: 5px;
    padding-right: 5px;
}

.equalizer-parameters {
    border-style: solid;
    border-color: white;
    border-width: 1px;
    background-color: black;
    padding: 5px;
}

.equalizer-sliders-container {
    clear: both;
    display: flex;
    flex-direction: row;

    .parameter-box {
        margin-right: 2px;
    }
}

.choose-effects {
    border: 5px solid #24293e;
    .delay-time-dropdown {
        width: 100%;
    }

    .Dropdown-root.is-open {
        margin-bottom: 160px;
        .Dropdown-control {
            width: 140px;
        }
    }

    .Dropdown-control {
        height: 20px !important;
        padding: 5px;
        line-height: 10px;
        width: 90px;
        border: 1px solid #4e5058;
        
        .Dropdown-arrow {
            top: 5px;
        }
        
    }
    
    .Dropdown-option {
        padding-left: 8px;
        padding-top: 3px;
        padding-bottom: 3px;
    }
}
.eq8-update-rate-dropdown-container {
    margin-left: auto;
}

.delay-time-dropdown {
    width: 180px;
}

.parameter-label {
    margin-top: 5px;
    display: flex;

    .parameter-label-name {
        margin-right: 5px;
    }

    .parameter-label-value {
        cursor: pointer;
        flex: 1;
    }

    .parameter-input-box {
        width: 100px;
        height: 20px;
        font-size: 10pt;
        background-color: #21263b !important;
        color: white !important;
    }
}


.midi-interface-effect {
    display: flex;
}

.envelope-dropdown {
    .Dropdown-menu {
        display: flex;
        flex-direction: column;
        width: 130% !important;
    }

    .pre-post-options {
        margin-top: 5px;
        display: flex;
        float: right;
    }
}

.pre-post-option {
    background-color: #717171;
    font-size: 8pt;
    margin-left: 3px;
    padding-right: 2px;
    padding-left: 2px;
    cursor: pointer;
    border-radius: 2px;
}

.pre-post-option.selected {
    background-color: #5802d9;
}

.note-mapping {
    .note-mapping-output {
        height: 20px;
        background-color: black;
        color: white;
    }

    .input-note {
    }

    display: flex;
}

.note-mapping.highlighted {
    background-color: #1a3e64;
}

.effects-chooser-container {
    display: flex;
    flex-direction: column;
}

.effects-chain-chooser {
    display: flex;
    margin-top: auto;
    text-align: center;
    background-color: #061a29;
    padding: 3px;
    height: 30px;
    margin-left: auto;
    margin-right: auto;
    
    .effects-chain-btn {
        margin-right: 10px;
        cursor: pointer;
    }
    
    .effects-chain-btn.selected {
        color: #43f4f1;
    }
    
    .new-effects-chain-btn {
        cursor: pointer;
        margin-left: 10px;
        
        
    }
}

.automation-label {
    font-size: 7px;
    background-color: #ff3a30;
    color: white;
    padding: 3px;
    border-radius: 3px;
    height: 15px;
}

.compressor-part {
    clear: both;
    margin-top: 45px;
}

.effect, {
    .slider-horizontal {
        width: 180px !important;
    }
}

.midi.effect.enabled {
    background-color: #1c1c1e; ;
}

.effect {
    .filter-dropdown-container {
        display: flex;
        float: none;

        .Dropdown-control {
            width: 80px;
        }
        
        .dropdown-label {
            margin-right: 3px;
        }
    }
}

.tidal-component {
    display: flex;

    .enter-button {
        padding: 3px;
        color: white;
        background-color: red;
        cursor: pointer;
    }

    .tidal-input {
        background-color: black;
        color: white;
        width: 300px;
    }

}
.error-box {
    height: 20px;
    color: red;
}



.parameter-box, .wider-sliderparameter-box {
    position: relative;
}
