.bottom-toolbar {
    display: flex;
    flex-direction: row;
}

.bottom-toolbar-left {
    flex: 1;
}

.contextual-help {
    background-color: black;
    opacity: 0.9;
    font-size: 11pt;
    color: #ff9e9e;
    width: 360px;
    height: 100px;
    border-style: solid;
    border-width: 1px;
    border-color: #6e050c;
    padding: 5px;
}

.contextual-help.empty {
    border-color: #6e050c;
}
