@import 'colors';

.colored-lfo-name-0 {
    color: red;
}

.lfo-0 {
    border-color: $lfo-color-1;
}

.toggled.lfo-0 {
    background-color: $lfo-color-1;
}

.colored-lfo-name-1 {
    color: $lfo-color-2;
}

.lfo-1 {
    border-color: $lfo-color-2;
}

.toggled.lfo-1 {
    background-color: $lfo-color-2;
}

.colored-lfo-name-2 {
    color: $lfo-color-3;
}

.lfo-2 {
    border-color: $lfo-color-3;
}

.toggled.lfo-2 {
    background-color: $lfo-color-3;
}

.colored-lfo-name-3 {
    color: $lfo-color-4;
}

.lfo-3 {
    border-color: $lfo-color-4;
}

.toggled.lfo-3 {
    background-color: $lfo-color-4;
}

.parameter-envelope-toggle {
    cursor: pointer;
    width: 10px;
    height: 10px;
    border-style: solid;
    border-width: thin;
    border-radius: 50%;
    float: left;
    margin-left: 5px;
    font-size: 5pt;
    text-align: center;
}

.parameter-lfo-toggle {
    cursor: pointer;
    width: 10px;
    height: 10px;
    border-style: solid;
    border-width: thin;
    float: left;
    margin-left: 5px;
    font-size: 5pt;
    text-align: center;
}

.lfo-boxes {
    margin-top: 5px;
    margin-bottom: 5px;
}

lfo-0 {
}

.instrument.external-instrument {
    min-width: 400px !important;
    padding: 10px;

    .midi-option {
        margin-left: 0px;
    }

    .resample-config {
        display: flex;

        .samples-per-note {
            display: flex;
            margin-left: 10px;
            margin-top: 10px;

            .dropdown-label {
                margin-right: 5px;
            }

            .Dropdown-option {
                padding: 2px;
            }
        }

        padding-bottom: 20px;
    }
}

.instruments-view {
    clear: both;
    padding: 5px;
    margin-top: 10px;
}

.instrument-section {
    float: left;
}

.instruments-header {
    font-size: 20pt;
}

.instrument-option {
    width: 400px;
    clear: both;
    font-size: 10pt;
    border: thin solid #292929;
    padding: 5px;
    margin-bottom: 2px;
    background-color: #1e1e1f !important;
}

.instrument-option:hover {
    background-color: #303032 !important;
}

.instrument-option-name {
    padding: 5px;
    cursor: pointer;
}

.instrument.synth {
    min-width: 650px !important;
}

.instrument.fm-synth {
    min-width: 700px;
}

.instrument {
    display: block;
    min-width: 650px;
    margin-bottom: 5px;
    font-size: 10pt;
    margin-left: 10px;
    float: left;
    background-color: #060c26;
    border-style: solid;
    border-width: 2px;
    border-color: #313233;
    border-radius: 5px;
}

.instrument-name {
    font-size: 20pt;
    cursor: pointer;
    float: left;
    margin-left: 5px;
}

.lfo-subsection-name {
    float: left;
}

.instrument-subsection {
    clear: both;
    margin: 5px;
    width: 200px;
    background-color: #121319;
    padding-right: 10px;
    padding-left: 10px;
    padding: 3px;
}

.instrument-subsection-name {
    font-size: 12pt;
    float: left;
}

.osc-type-dropdown-container {
    float: right;
    margin-top: 5px;
}

.osc-type-dropdown {
    width: 110px;
    height: 10px;
}

.osc-type-control-dropdown {
    height: 20px !important;
    padding: 2px !important;
    padding-left: 5px !important;
    background-color: black !important;
    color: white !important;
}

.osc-type-arrow-dropdown {
    top: 7px !important;
    color: white !important;
}

.lfo-rate-label {
    margin-right: 5px;
}

.midi-selected {
    border-style: solid;
    border-color: #ff8c8c;
}

.parameter-box {
    width: 160px;
    margin-right: 20px;
}

.lfo-component.selected, .envelope-component.selected {
    border-style: solid;
    border-color: #5701d6; 
}

.lfo-component, .envelope-component {
    .osc-type-dropdown {
        width: 70px;
    }

    font-size: 8pt;
    background-color: #1d1d1e;
    padding: 10px;
    margin-right: 5px;
    border-width: 1px;
    min-width: 220px; 

    .mods-button {
        float: right;
    }

    .parameter-box {
        margin-right: 0px;
    }
}

.lfo-track-foreground-1 {
    color: $lfo-color-1;
}

.lfo-track-background-1 {
    color: $lfo-color-1;
}

.lfo-track-foreground-2 {
    color: $lfo-color-2;
}

.lfo-track-background-2 {
    color: $lfo-color-2;
}

.lfo-track-foreground-3 {
    color: $lfo-color-3;
}

.lfo-track-background-3 {
    color: $lfo-color-3;
}

.lfo-track-foreground-4 {
    color: $lfo-color-4;
}

.lfo-track-background-4 {
    color: $lfo-color-4;
}

.lfo-thumbnail.selected {
    color: white;
    background-color: #3103a0;
}

.lfo-thumbnail {
    cursor: pointer;
    height: 40px;
    margin-bottom: 5px;
    width: 60px;
    background-color: #black;
    text-align: center;
    font-size: 14pt;
    line-height: 40px;
    color: #ababab;
    border-style: solid;
    border-width: 1px;
    border-color: #3102a0;
}

.track.lfos-track.mix-lfo-view {
}

.lfos-track {
    margin-top: 5GGpx;
}

.lfo-synced-checkbox {
    float: right;
}

.lfo-rate-dropdown {
    display: flex;
}



.slider.slider-horizontal .slider-selection, .slider.slider-horizontal .slider-track-low, .slider.slider-horizontal .slider-track-high {
    background-color: #272727;
}

.slider-vertical > .slider-track {
    background-image: linear-gradient(to bottom,#afb9ff,#f9f9f9 100%);
}

.slider-horizontal > .slider-track > .slider-selection {
    background-image: linear-gradient(to left,#afb9ff,#f9f9f9 100%);
}

.slider-vertical > .slider-track > .slider-selection {
    background-color: #272727;
    background-image: none;
}

.parameter-label {
    cursor: pointer;
}


.modal-content {
    background-color: #0a0c15 !important;
    color: #ecffff;
    border: 5px solid rgb(5, 17, 26) !important;
}

.modal-header {
    border-bottom: 2px solid #191919 !important;
}

.modal-footer {
    border-top: 2px solid #191919 !important;
}

.close {
    > span {
        color: white;
    }
}

.roll-factor-dropdown {
    width: 120px !important;
}

.oscillator-option {
    padding-top: 2px;
    padding-bottom: 2px;
    padding-right: 5px;
    padding-left: 5px;
    border-style: solid;
    border-width: 1px;
    margin-left: 5px;
    cursor: pointer;
}

.oscillator-option.selected {
    background-color: white;
    color: black;
}

.oscillator-option.disabled {
    background-color: grey;
    opacity: 0.4
}

.oscillator-options {
    clear: both;
    display: flex;
    font-size: 6pt;
}

.oscillator-subsection-header {
    float: left;
}

.pads-pagination-container {
    clear: both;
    float: right;
    display: flex;
    .pads-pagination-button {
        padding-right: 5px;
        padding-left: 5px;
        height: 20px;
        cursor: pointer;
        line-height: 16px;
        border-style: solid;
        border-width: 1px;
    }
}

.key {
    padding: 15px;
    cursor: pointer;
    text-align: center;
    border-style: solid;
    float: left;
    margin: 5px;
    border-color: #082435;
}

.pad {
    cursor: pointer;
    text-align: center;
    border-style: solid;
    width: 60px;
    height: 60px;
    line-height: 50px;
    float: left;
    margin: 5px;
    border-color: #082435;
}

.pad.mini {
    font-size: 8pt;
    line-height: 25px;
    width: 28px;
    height: 28px;
    border-width: 2px;
}

.key.mini {
    padding: 3px;
    padding-right: 5px;
    padding-left: 5px;
    font-size: 8pt;
    border-width: 2px;
}

/* keyboard */

.keyboard {
    margin-left: 10px;
}

.keyboard-row {
    clear: both;
}

.keyboard-row-2 {
    margin-left: 10px;
}

.keyboard-row-3 {
    margin-left: 30px;
}

.draw-envelope-modal-button {
    background-color: #373737;
    text-align: center;
    cursor: pointer;
}

.envelope-modal {
    max-width: 700px !important;
}

.envelope-parameters-container {
    margin-top: 10px;
    display: flex;

    .osc-type-dropdown, .osc-type-dropdown-container, .osc-type-dropdown-control {
        width: 100px;
        float: left;
    }

    .envelope-parameters {
        margin-left: 10px;

        .selected-curve-label {
            color: #e7265b;
        }

        .envelope-parameter {
            margin-right: 5px;
            
            .envelope-value {
                margin-left: 2px;
                color: #e7265b;
            }
        }
    }
}

.osc-type-dropdown-container.selected {
    border-style: solid;
    border-width: 2px;
    border-color: #5805d8;
    height: 24px;
}

.external-instrument-name {
    font-size: 14pt;
}

.external-instrument-subsection {
    background-color: #5047c6;
    padding: 2px;
}

.envelope-value-container {
    width: 100%;
    height: 20px;

    .envelope-value {
        background-color: #5904ca;
        height: 20px;
        opacity: 0.7;
    }
}

.lfo-effects-view {
    overflow: scroll;
}

.fm-parameters {
    width: 250px;
    float: left;
    margin-right: 5px;
    background-color: #121319;
    padding: 3px;
    
    .oscillator-types {
        display: flex;
        .osc-type-dropdown-container {
            float: none;
            width: 100px;
            
            .Dropdown-control {
                width: 70px;
            }
        }
        
        .oscillator-type {
            width: 70px;
        }
        
        .mod-label {
            flex: 1;
            text-align: center;
            line-height: 30px;
        }
        
        .partial-count-container {
            display: flex;
            margin-top: 12px;
            
            .partial-count {
                margin-right: 4px;
                background-color: black;
                color: white;
                padding: 1px;
                
                cursor: pointer;
            }
        }
    }
}

.fm-genetix {
    padding: 10px;

    .back-button {
        padding: 3px;
        font-size: 10pt;
        background-color: #030047;
    }

    .loading-spinner {
        font-size: 14pt;
        margin-top: auto;
        margin-bottom: auto;
        margin-left: auto;
        margin-right: auto;
    }

    .presets {
        padding-top: 10px;

        .preset {
            cursor: pointer;
            padding: 5px;
        }

        .preset.odd {
            background-color: #161c37;
        }

        .preset:hover {
            background-color: #1f1f1f;
        }
    }

    .pagination-buttons {
        display: flex;
        margin-top: 15px;
        .pagination-button {
            margin-right: 15px;
            background-color: #3c397e;
            padding: 5px;
            cursor: pointer;
        }
    }

    .crossbreed-probability {
        margin-top: 15px;
    }

    .crossbreed-option {
        padding: 5px;
        background-color: gray;
        color: white;
        cursor: pointer;
        margin-left: 10px;
    }

    .crossbreed-option.enabled {
        background-color: green;
        color: white;
    }
}

.instrument-header {
    background-color: #27272a;
    padding-left: 5px;
    font-size: 11pt;
}

.instrument {
    .osc-type-dropdown {
        width: 80px;
    }
}

.harmonics-button {
    padding: 3px;
    font-size: 9pt;
    background-color: #4306ff;
    color: white;
    cursor: pointer;
}


.harmonics-editor {
    width: 200px;

    .partial-editor {
        display: flex;

        .partial-number {
            width: 40px;
        }

        .slider {
            width: 100px;
        }
    }
}

.lfo-waveform {
    margin-top: 20px;
}

.track-selection-section {
    display: flex;

    .label {
        margin-right: 10px;
    }
}

.horizontal-piano-container {
    overflow: scroll !important;
}

.horizontal-piano {
    display: flex;
    margin-top: 5px;

    .octave {
        display: flex;
        position: relative;

        .octave-label {
            position: absolute;
            left: 2px;
            top: 2px;
            color: black;
            font-size: 7pt;
        }

        .key-container.selected {
            border: 1px solid red;
        }

        .key-container.last-played {
            border-top: 3px solid #36ffa2;
        }

        .key-container {
            display: flex;
            flex-direction: column;
            
            .key {
                width: 12px;
                padding: 0px;
                height : 50px;
                border: 1px solid black;
                background-color: #93a9e6;
                opacity: 1;
                margin: 0px;
            }
            
            .black.key {
                background-color: black;
            }
            
            .key-info.on {
                opacity: 1;
            }

            .key-info {
                opacity: 0.2;
                height: 20px;
                width: 12px;
                cursor: pointer;
                font-size: 6pt;
                text-align: right;
                line-height: 30px;
                padding-right: 2px;
            }
        }
    }
}

.sampled-instrument ::-webkit-scrollbar-thumb {
    background: #404879 !important;
}

.sampled-instrument {
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    scrollbar-color: red blue;
    
    /*
      Introduced in Internet Explorer 10.
     S           ee http://ie.microsoft.com/testdrive/HTML5/msUserSelect/
   */
    -ms-user-select: none;
    user-select: none;
    width: 550px;
    min-width: 550px;
    border-color: #292a2b;
    background-color: #19191b;
    

    .loop-container {
        display: flex;
    }

    .advanced-controls {
        width: 240px;
        display: flex;
        flex-direction: column;
        
        .piano-icon {
            cursor: pointer;
            width: 15px;
            height: 15px;
            background-color: white;
            margin-left: auto;
            opacity: 0.4;
        }

        .piano-icon.on {
            opacity: 1;
        }

        .sampler-dropdowns {
            display: flex;
            
        }

        .checkbox-parameter {
            label {
                margin-bottom: 0px !important;
            }
        }

        .drop-here {
            text-align: center;
            flex: 1;
            line-height: 300%;
        }

        .sample-search-container {
            margin-top: 5px;
            .search-box-container {
                width: 100%;
                display: flex;

                .search-button {
                    cursor: pointer;
                    margin-left: 5px;
                    padding: 2px;
                    color: #ff6d44;
                    background-color: #352f2f;
                }
            }

            .no-results {
                text-align: center;
                color: red;
            }

            .suggestions {
                clear: both;
                max-height: 200px;
                overflow-y: scroll;
                margin-top: 5px;

                .suggestion {
                    cursor: pointer;
                }

                .suggestion:hover {
                    background-color: #292831;
                }
            }
            .sample-search {
                width: 100%;
                background-color: #292831;
                color: white;
            }
        }

        .samples-container {
            display: flex;

            .add-sample-button {
                line-height: 5px;
                font-size: 16pt;
                width: 20px;
                height: 20px;
                color: #ff6d44;
                background-color: #352f2f;
                margin-left: 5px;
                cursor: pointer;
            }

            .sampled-sound {
                width: 20px;
                height: 20px;
                text-align: center;
                color: black;
                cursor: pointer;
            }

            .selected.sampled-sound {
                border-bottom: 3px solid white;
            }
        }
    }

    .parameter-mod {
        width: 315px;
        .number-input {
            width: 55px !important;
        }
    }

    .waveform-toolbar {
        background-color: #121319;
        display: flex;

        .next-button {
            cursor: pointer;
            border: solid white;
            border-width: 0 3px 3px 0;
            display: inline-block;
            padding: 3px;
            height: 8px;
            margin-top: 5px;
            -webkit-transform: rotate(-45deg);
        }

        .sample-chooser-buttons {
            display: flex;
            background-color: #2f2e2f;
            padding-right: 5px;
            margin-right: 5px;
            
            .previous-button:hover, .next-button:hover {
                border-color: #56ff15;
            }

            .previous-button {
                cursor: pointer;
                border: solid white;
                border-width: 0 3px 3px 0;
                display: inline-block;
                padding: 3px;
                height: 8px;
                margin-top: 5px;
                margin-left: 5px;
                -webkit-transform: rotate(135deg);
            }
        }
        
        .delete-button {
            margin-top: 5px;
            color: red;
            margin-left: auto;
            margin-right: 5px;
        }
        .sample-name {
            background-color: black;
            color: white;
            padding: 3px;
            max-width: 120px;
            margin-top: 0px;
                           
        }
    }

    .parameters-container {
        margin-top: 10px;
        display: flex;

        .loop-parameter {
            margin-top: 5px;
            label {
                margin-bottom: 0px;
            }

            .sample-name {
                background-color: #2d2d2f;
            }
        }

        .adsr-container, .advanced-controls, .sampler-parameters {
            border: 1px solid #252729;
            background-color: #121319;
        }
        
        .sampler-parameters {
            display: flex;

            .sample-delete-button {
                margin-left: auto;
                padding: 3px;
                font-size: 8pt;
                background-color: red;
                color: white;
                cursor: pointer;
            }
        }

        .advanced-controls {
            margin-left: auto;
            padding: 5px;

            .interpolation-dropdown-container {
                display: flex;

                .Dropdown-option {
                   padding: 2px;
                }

                .Dropdown-control {
                    height: 16px;
                    width: 120px;
                    line-height: 1px;
                    font-size: 8pt;
                    border-color: #383737;
                }

                .Dropdown-arrow {
                    top: 5px;
                }

                .label {
                    margin-right: 5px;
                    padding: 0px;
                    text-align: left;
                    font-size: 8pt;
                    flex: 1;
                }
            }
        }
            
        .pane {
           .adsr-container, .parameters-row{
                display: flex;
                
                .parameter-knob-container {
                    width: 70px;
                }
            }
        }
    }

    .sample-info {
        display: flex;

        .sample-name-thumb {
            width: 25px;
            height: 25px;
            cursor: pointer;
        }


   }
}

.knob-label {
    font-size: 8pt;
    cursor: pointer;
}

.parameter-modulators {
    display: flex;

    .lfo-buttons, .envelope-buttons {
        width: 30px;

        .parameter-lfo-toggle, .parameter-envelope-toggle {
            margin-left: 0px;
            margin-right: 5px;
        }
        
    }
}

.multiple-parameter-slider {
    padding: 5px;
    .slider-and-modulators {
        display: flex;
        margin-top: 5px;

        .parameter-modulators {
            margin-left: 15px;
        }
    }

    .simple-parameter-slider {
        padding-left: 5px;
    }
    
    .parameters {
        display: flex;
        float: none;
        margin-left: 0px;

        .selected.parameter-button {
            background-color: #3204a1;
        }

        .parameter-button {
            cursor: pointer;
            margin-right: 5px;
            padding-right: 5px;
            padding-left: 5px;
            width: 40px;
            
            .parameter-name {
                border-bottom: 1px solid white;
                text-align: center;
            }

            .parameter-value {
                text-align: center;
                font-size: 8pt;
            }
        }
    }
}

.sample-synth-button {
    margin-top: 5px;
    padding: 5px;
    border: 1px solid #f92360;;
    color: #f92360;;
    width: 100px;
    cursor: pointer;
}

.sample-synth-button.sampling {
    background-color: #f92360;;
    color: white;
    width: 130px;
}

.tab-options {
    display: flex;

    .parameter-option {
        padding: 3px;
        margin-right: 3px;
        border-top-right-radius: 3px;
        background-color: #2c2e31;
        font-size: 12px;
        cursor: pointer;
    }

    .parameter-option.selected {
        background-color: #3407a3;
    }
}

.loop-finder {
    padding: 5px;
    .loop-button {
        padding: 5px;
        background-color: #00a52c;
        color: white;
        cursor: pointer;
    }
}


