@import 'colors';

.sub-sequences-label {
    margin-left: 5px;
    font-size: 8pt;
}

.view-dropdown {
    float: left;
}

.main-toolbar {
    height: 40px !important;
    background-color: #1e1e1f;
    opacity: 0.98;
    z-index: 20;
}

.patterns-container {
    margin-left: 15px;
    float: left;
}

.new-pattern-btn {
    margin-top: 6px;
    margin-left: 20px;
    float: left;
    cursor: pointer;
    color: white;
    text-align: center;
    font-size: 25px;
    line-height: 26px;
    height: 28px;
    width: 28px;
}

.new-pattern-btn:hover {
    background-color: white;
    color: black;
}

.delete-pattern-btn:hover {
    background-color: white;
    color: black;
}

.delete-pattern-btn {
    float: left;
    cursor: pointer;
    color: white;
    margin-top: 6px;
    text-align: center;
    font-size: 25px;
    line-height: 26px;
    height: 28px;
    width: 28px;
}

.pattern-box:hover {
    background-color: white;
    color: #315656;
}

.pattern-box {
    margin-top: 8px !important;
    float: left;
    cursor: pointer;
    color: white;
    margin: 5px;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 12pt;
}

.current-pattern {
    float: left;
    background-color: white;
    
    padding-left: 5px;
    padding-right: 5px;
    font-size: 12pt;
    margin: 5px;
    color: #315656;
}

.top-toolbar {
    display: flex;
    flex-direction: column;
}

.sub-toolbar {
    display: flex;
    flex-direction: row;
}

.done-button {
    background-color: #333333;
    color: white;
    cursor: pointer;
    border-radius: 2px;
    padding: 3px;
    font-size: 12px;
    line-height: 30px;
}

.color-picker {
    display: flex;
    flex-direction: row;

    background-color: #231b1b;
    padding: 10px;

    .color-option {
        border-radius: 50%;
        width: 20px;
        height: 20px;
        margin-right: 10px;
        cursor: pointer;
    }
    
    .color-option:hover {
        border-style: solid;
        border-color: gray;
        border-width: thin;
    }

    .color-option.selected {
        border-style: solid;
        border-color: white !important;
        border-width: 3px !important;
    }
}

.editing-color {
    border-style: solid;
    border-width: thin;
    border-color: white;
}

.loop-icon.looping {
    background-color: yellow;
}

.loop-icon {
    cursor: pointer;
    float: right;
    margin-top: 2px;
    width: 20px;
    height: 20px;
}

.mix-buttons {
    float: left;
    background-color: #333333;
    border-style: solid;
    border-color: #211b1b;
    margin: 5px;
    padding-left: 5px;
    padding-right: 5px;
    width: 120px;
    margin-left: 20px;

    .stop-button {
        float: left;
        width: 12px;
        height: 12px;
        background-color: white;
        margin-top: 5px;
        margin-right: 5px;
        cursor: pointer;
    }

    .piano-roll-button {
        width: 18px;
        height: 18px;
        float: right;
        margin-left: 7px;
        margin-top: 3px;
    }

    .piano-roll-button.show {
        background-color: #69b877;
    }
}

.clock > .time {
    width: 25px;
    float: left;
    text-align: center;
}

.clock > .metronome {
    float: left;

    cursor: pointer;
    padding-left: 2px;
    line-height: 22px;
    height: 25px;

    .dot {
        width: 8px;
        height: 8px;
        margin-right: 2px;
        border: 1px solid white;
        background-color: #333333;
    }
}

.clock {
    .metronome.on {
        background-color: #ffee37;
        
        .dot.first {
            border: 1px solid black;
            background-color: black;
        }
        
        .dot.second {
            border: 1px solid black;
            background-color: #ffee37;
        }
    }

    .metronome.on.off-beat {
        .dot.first {
            border: 1px solid black;
            background-color: #ffee37;
        }

        .dot.second {
            border: 1px solid black;
            background-color: black;
        }
    }
}


.clock > .track-time {
    width: 32px;
    float: left;
    text-align: right;
    margin-right: 10px;
}

.clock > .divider {
    float: left;
}


.toolbar-sticky {
    z-index: 5;
}

.z-index-hack {
    z-index: -1;
}

.sticky-keyboard {
    top: 40px !important;
    height: 400px;
}


.dropdown-menu.show {
    background-color: #272727;
    color: white;
    opacity: 0.95;
}

.dropdown-item {
    color: white !important;
}

.dropdown-item:hover {
    color: black !important;
}

.roll-button {
    border-radius: 5px;
    border-color: red;
    border-style: solid;
    border-width: thin;
    margin-top: 11px;
    padding: 2px;
    float: left;
    font-size: 7pt;
    cursor: pointer;
    background-color: #f4225e;
    color: white;
}

.roll-button.rolling {
}

.connection-status {
    display: flex;
    float: left;
    background-color: #333333;
    border-style: solid;
    border-color: #211b1b;
    margin: 5px;
    color: white;
    font-size: 10pt;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: 20px;
}

.connection-status.SYNCED {
    border-color: #9fdca9;
    color: #d2ffc4;
}

.connection-status.WAITING {
    border-color: orange;
}

.clock {
    float: left;
    cursor: pointer;
    background-color: #333333;
    border-style: solid;
    border-color: #211b1b;
    margin: 5px;
    padding-left: 5px;
    font-size: 12pt;
    margin-left: 20px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: relative;
}

.clock.percentage-25:after {
    content:'\A';
    position:absolute;
    background:black;
    opacity: 0.35;
    top:0; bottom:0;
    left:0; 
    width:25%; /* Specify the width.. */
}

.clock.percentage-50:after {
    content:'\A';
    position:absolute;
    background:black;
    opacity: 0.35;
    top:0; bottom:0;
    left:0; 
    width:50%; /* Specify the width.. */
}

.clock.percentage-75:after {
    content:'\A';
    position:absolute;
    background:black;
    opacity: 0.35;
    top:0; bottom:0;
    left:0; 
    width:75%; /* Specify the width.. */
}

.clock.percentage-100:after {
    content:'\A';
    position:absolute;
    background:black;
    opacity: 0.35;
    top:0; bottom:0;
    left:0; 
    width:100%; /* Specify the width.. */
}

.clock-type {
    color: red;
    margin-left: 5px;
    background-color: black;
    line-height: 16px;
    border-radius: 4px;
    padding: 2px;
    font-size: 8pt;

    .connection-status {
        padding: 5px;
        font-size: 8pt;
        background-color: #303955;
        margin-left: auto;
    }
}

.sub-sequences-container {
    background-color: #333333;
    border-style: solid;
    padding-right: 20px;
    padding-left: 5px;
    border-color: #211b1b;
    display: flex;
    margin: 5px;
    float: left;
    margin-left: 20px;

    .new-sub-sequence {
        margin-left: 20px;
        padding: 2px;
        line-height: 18px;
        height: 24px;
        cursor: pointer;
    }

    .delete-sub-sequence {
        padding: 2px;
        line-height: 18px;
        margin-left: 5px;
        height: 24px;
        cursor: pointer;
    }

    .new-sub-sequence:hover, .delete-sub-sequence:hover {
        background-color: white;
        color: black;
    }

    .sub-sequence {
        font-size: 8pt;
        padding: 2px;
        margin-top: 3px;
        height: 18px;
        cursor: pointer;
        margin-left: 4px;
        margin-right: 4px;
    }

    .sub-sequence.selected {
        background-color: white;
        color: black;
    }
}

.guilds-menu {
    .guild-name {
    }

    .guild-user-name {
        margin-right: 5px;
    }

    .not-recording-dot.logged-out {
    }

    .not-recording-dot {
        margin-right: 10px;
        background-color: #6200ff;
    }

    .not-recording-dot.logged-out {
        background-color: #5a556e;
    }

    .dropdown-item.user-name-item {
        border-bottom: 1px solid #393b3b;
    }

    .dropdown-item.user-name-item:hover {
        background: none;
        .guild-user-name {
            color: white;
        }
    }

    .dropdown-item.logout-item {
    }
}

#password {
    padding-left: 10px;
}

.go-back-button {
    float: left;
    margin-right: 5px;
    font-size: 10pt;
    background-color: #600ed7;
    color: white;
    border-color: #211b1b;
    border-width: thin;
    border-radius: 2px;
    padding: 5px;
    cursor: pointer;
}

.pending-transactions {
    float: left;
    margin-right: 5px;
    font-size: 8pt;
    background-color: #333333;
    border-style: solid;
    padding: 5px;
    border-color: #211b1b;
    display: flex;
}

.command-button {
    border-style: none;
    font-size: 8pt;
    cursor: pointer;
    background-color: #6000f9;
}

.bpm-component {
    float: left;
    margin-left: 50px;
    background-color: #333333;
    border-style: solid;
    border-color: #211b1b;
    margin: 5px;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 12pt;
    margin-left: 20px;
}

.resolution-dropdown  {
    .Dropdown-control {
        height: 38px;
    }
}



.sequencer-buttons-container.loading {
    pointer-events: none;
    opacity: 0.2;
}
