.group-component.selected {
}

.group-component {
    display: flex;
    clear: both;
    padding: 5px;
    border-style: solid;
    border-width: 7px;
    border-color: #0b1018;
    margin-bottom: 10px;
    background-color: black;
    margin-right: 10px;

    .badge {
        font-size: 6pt;
    }

    .youtube-name {
        font-size: 8pt;
    }

    .group-thumbnail {
        cursor: pointer;
        height: 80px;
        max-width: 80px;
        border-style: solid;
        border-width: thin;
        border-color: #092435;
        display: flex;

        .selected {
            border-color: #ff5454 !important;
            border-style: solid;
            border-width: 2px !important;
        }

        .group-part-wrapper {
            display: flex;
            flex-direction: column;
            border-style: solid;
            border-color: #3d9bd2;
            border-width: 1px;

            .group-part-thumbnail {
                flex: 1;
            }

            .part-footer {
                height: 10px;
            }
        }
    }
}

