$negativeSpaceGrey : #aaabab; 
$backgroundGrey: #E2E2E2;
$textColor: #2b2b2b;

.installation-landing-page {
    display: flex;
    flex-wrap: wrap;
    margin: 30px;
    justify-content: center;
    color: $textColor;

    .break {
        flex-basis: 100%;
        height: 0;
    }

    .installation-hero {
        margin-top: 30px;
        background-color: $backgroundGrey;

        .title {
            font-size: 48pt;
            text-align: center;
            line-height: 70px;
            margin: 20px 20px 0px 20px;
        }
        .authors {
            font-size: 14pt;
            text-align: center;
            margin-top: 10px;
            margin-bottom: 20px;
        }

        .installation-info {
            cursor: pointer;
            background-color: #4fc7ff;
            font-size: 12pt;
            text-align: center;
        }
    }

    .installation-tiles {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        .installation-tile {
            background-color: $backgroundGrey;
            width: 400px;
            height: 350px;
            margin: 50px 50px 0px 50px;

            .installation-tile-info-name {
                margin-top: 40px;
                background-color: $negativeSpaceGrey;
                font-size: 18pt;
                text-align: center;
            }
        }

        .installation-tile-info-enter {
            cursor: pointer;
            width: 400px;
            height: 50px;
            margin-left: 50px;
            margin-right: 50px;
            font-size: 18pt;
            line-height: 50px;
            text-align: center;
            background-color: #6cffa7;
        }
    }

    .installation-loading-rooms {
        margin-top: 50px;
        text-align: center;
        background-color: $backgroundGrey;

        .installation-loading-rooms-content {
            margin: 20px;
        }
    }
}

.installation-info-modal {
    .modal-content {
        border-style: none !important;
        border-radius: 0px; 
        background-color: $backgroundGrey !important;
        color: $textColor;
        font-size: 14pt;
    }
}

.installation-multiple-tabs {
    text-align: center;
    background-color: $backgroundGrey;
    color: #2b2b2b;
    margin-top: 60px;
    margin-right: auto;
    margin-left: auto;

    .installation-multiple-tabs-text {
        margin: 10px;
        font-size: 20pt;
    }

    .installation-multiple-tabs-error {
        margin: 10px;
        font-size: 10pt;
    }
}
