$negativeSpaceGrey : #aaabab; 
$backgroundGrey: #cccbcb;
$backgroundDark : #3c3a4a;
$textColor: #c9c6c6;

html {
    background-color: $negativeSpaceGrey !important;
}

.piano-roll-wrapper {
    position: relative;
    
    .parameter-automation {
        position: absolute;
        top: 28px;
        width: 100%;
        height: 100%;
        background-color: black;
        opacity: 0.7;
        z-index: 50;
    }
}

.installation {
    .installation-knobs {
        padding: 5px;
        display: flex;
        background-color: $backgroundDark;
        flex: 0;
        opacity: 0.9;

        .parameter-knob {
            margin: 5px;

            .parameter-name {
                min-width: 65px;
            }
        }
    }
    .automation-toolbar {
        .dropdowns {
            // display: none;
        }

        .parameter-button {
            padding: 5px;
            font-size: 13pt;
        }
    }

    .delete-button {
        display: none;
    }

    .mix-buttons {
        margin-left: 15px;
        margin-top: 15px;
    }

    .toolbar-resizer {
        .parameter-button {
            background-color: #aaabab;
        }
    }
}

.track-controls {
    margin-left: 5px;
}

.track-solo-button, .midi-recording-button, .youtube-container, .track-right-buttons, .track-meter {
    display: none;
}

.steps-container {
    margin-right: 10px;
}

.mute-button {
    width: 25px;
    height: 25px;
    font-size: 10pt;
}

.automation-toolbar {
    display: flex;
    height: 20px;
    background-color: #141415;
    
    .dropdowns {
        display: flex;
    }
    
        .dropdowns.disabled {
            pointer-events:none;
            opacity: 0.3;
        }

        .automation-icon {
            height: 20px;
            width: 20px;
            cursor: pointer;
            margin-left: 5px;
        }

        .toolbar-resizer {
            flex: 1;
            display: flex;
            margin-left: 5px;

            .parameter-button {
                margin-left: 5px;
                font-size: 8pt;
                padding: 3px;
                border-radius: 3px;
                line-height: 15px;
                cursor: pointer;
            }
            
        }

        .Dropdown-option {
            font-size: 10pt !important;
            padding-top: 2px !important;
            padding-bottom: 2px !important;
            padding-left: 10px !important;
        }

        .Dropdown-option.has-automations {
            color: #f3235e !important;
        }

        .Dropdown-control {
            border: none;
            height: 20px;
            line-height: 5px;
            font-size: 10pt;
            opacity: 0.9;
            border-radius: 0px;

            .Dropdown-arrow {
                top: 7px;
            }
        }
    }


.installation, .show-piano-roll {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                          supported by Chrome and Opera */
 
    display: flex;
    flex-direction: column;
    height: 100%;
    .arrangement-resizing-edge {
        width: 100%;
        height: 10px;
        background-color: #5c1bc7;
        opacity: 0.5;
        cursor: row-resize;
    }

    .scroll-area {
        height: 100%;
        flex: 1;
        overflow-y: scroll;
    }

    .arrangement-bottom-container {
        z-index: 20;
        overflow-y: hidden;
        .piano-roll-container {
            padding-top: 0px;
        }
    }
}


.installation {
   
    .guilds-menu, .midi-input-dropdown-container, .lock-button, .piano-roll-button, .patterns-component {
        display: none;
    }
    
    .sequencer-dashboard {
        display: flex;
        margin: 30px;
        z-index: 10;

        .dashboard-module {
            margin-right: 15px;
            padding: 5px;
            width: 125px;
            height: 125px;
            background-color: $backgroundDark;
            color: #a5a5a5;
            opacity: 0.9;

            .module-name {
                font-size: 12pt;
            }

            .inner-module {
                font-size: 10pt;
            }
        }

        .wide {
            width: 250px;
        }
    }

    .main-area {
        background: none;
    }

    .tracks-container {
        flex: 0;
        margin-left: 15px;
        z-index: 20;

        .track-right-buttons {
            margin-right: 20px;
        }
        .track-footer-container {
            display: none;
        }

        .delete-button {
        }
    }

    .bpm-component {
        opacity: 0.8;
    }


}

.youtube-info {
    .volume-box {
        margin-top: 3px;
        margin-left: 0px;
        height: 20px;
        
        .volume-indicator {
            height: 20px;
        }
    }
}


.midi-input-dropdown-container { 
    display: none;
}



.installation {
    .dashboard-module {
        color: $textColor !important;
    }

    .piano-roll-container {
        .note-blank-side {
            background-color: $backgroundGrey;
            color: #4f5252;
        }

        .time-grid-step.current-step, .time-grid-step.bar.current-step, .time-grid-step.odd.current-step {
            border-color: red !important;
            width: 4px !important;
        }

        .time-grid-step.current-step.odd {
            background: none;
        }

        .time-grid-step {
            border-color: #aaacb2;
        }

        .time-grid-step.bar {
            border-left-color: #a7a7a8;
        }

        .piano-roll-row.black {
            background-color: #bebebe;
        }

        .piano-roll-row {
            background-color: #dbdbdb;
        }

        .time-grid-step.odd {
            background-color: #a3a3af;
            border-color: #4a4569;
        }

        .flashit {
            color: #ff4242 !important;
        }

        .piano-roll-cursor {
            width: 4px;
        }

        .black-note.note-name {
            background-color: black;
        }

        .white-note.note-name {
            background-color: white;
        }
    }




    .automation-toolbar {
        background-color: $backgroundGrey;

        .Dropdown-control {
            background-color: #959696 !important;
        }
    }

    .track {
        .track-header {
            background: $negativeSpaceGrey !important;
            .track-name-view {
                background: $backgroundGrey;
                color: $textColor;
                border-radius: 0px;
                padding-left: 8px;
                padding-right: 10px;
                padding-top: 5px;
                padding-bottom: 0px;
                line-height: 15px;
                font-size: 10pt;
            }

            .track-tags {
                display: none;
            }
        }

        .track-inner {
            padding: 10px;
        }

        .track-controls {
            display: none;
        }

        .step.off {
            background: none;
        }

        border: none;
        border-radius: 0px;
        background-image: none;
        background-color: $backgroundGrey !important;
    }

    .mix-buttons {
        background: none;
        .stop-button, .not-recording-dot, .recording-dot {
            display: none;
        }

        .playpause input[type="checkbox"]:checked + label {
            height: 40px;
            border-width: 40px 0px 40px 60px;

            cursor: pointer;
        }

        .playpause label {
            margin-left: 15px;
            border-width: 0px 0 0px 50px;
            height: 70px;
        }
    }
}

.installation.sequencer-container {
    background-color: $negativeSpaceGrey !important;
    background-image: none;
}

.tracks-chooser-toolbar {
    display: flex;
    background-color: $backgroundGrey;

    .track-option.disabled-by-constraint {
        pointer-events: none;
        opacity: 0.2;
    }
    
    .track-option {
        position: relative;
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 5px;
        padding-bottom: 5px;
        font-size: 12pt;
        margin-right: 20px;
        background-color: #7E7C7C;
        color: white;
        cursor: pointer;
        overflow: hidden;
        white-space: nowrap;

        .track-dot {
            position: absolute;
            border-radius: 8px;
            left: 2px;
            top: 23px;
            width: 8px;
            height: 8px;
        }
    }

    .track-option.selected {
        color: black;
    }
}

.hide {
    opacity: 0%;
    pointer-events: none;
}

.loading-screen {
    display:table;
    text-align: center; 
    height: 100%;
    width: 100%;

    .progress-bar {
        position: absolute;
        left: 35%;
        top: 40%;
        width: 400px;
        height : 50px;
        background-color: #4e4e56;
        padding: 5px;
        z-index: 500;
        border-radius: 0px;
        opacity: 0.8;
        
        .inner-progress-bar {
            position: relative;
            .progress {
                border-radius: 0px;
                height: 40px;
                background-color: red;
                background-image: linear-gradient(to right,#58587d,#2b8b92);
            }

            .loading-text {
                z-index: 6000;
                position: absolute;
                left: 20px;
                color: #d7d1d1;
                font-size: 14pt;
                top: 6px;
            }
        }
    }
}

.installation-row {
    display: flex;
    margin-top: 10px;

    .contextual-help {
        margin-left: 25px;
        width: 300px;
        background-color: #3c3b49;
        border-style: none;
        color: $textColor;

        opacity: 0.8;
    }

    .contextual-help.empty {
        opacity: 0.1;
    }
}
