.sends.track {
    border-color: #1e1e1f;;
    background: linear-gradient(#1b1c1e, #0c0c0d 40%);

    .track-header {
        background-color: #1e1e1f;
    }

    .sends-container {
        display: flex;
        padding: 5px;

        .send {
            margin-right: 10px;
            
            .send-name {
                padding: 0px;
                font-size: 10pt;
                margin-bottom: 10px;
            }

            .send-effect {
                background-color: #2a2636;
                border-radius: 3px;
                padding: 3px;
                font-size: 8pt;
                margin-bottom: 3px;
                cursor: pointer;
            }

            .send-effect.selected {
                background-color: #5700d8;
            }

            .send-effects-dropdown {
                font-size: 8pt;
                
                .Dropdown-control {
                    height: 20px;
                    line-height: 5px;
                }

                .Dropdown-arrow {
                    top: 7px;
                }
            }
        }
    }

    .new-send-button {
        padding: 5px;
        font-size: 10px;
        margin-top: auto;
        background-color: blue;
        margin-bottom: 5px;
        margin-left: auto;
        cursor: pointer;
    }
}
